
#expertise__section{

    &-1 {
        &__paint{
            top: 20%;
            width: 13%;
        }
        &__mobile{
            bottom: 15%;
            right: 30%;
            width: 8%;
        }
        &__laptop{
            bottom: 10%;
            left: 10%;
            width: 20%;
        }

        @media only screen and (max-width: $screen-large) {
            &__paint{
                top: 20%;
                width: 20%;
            }
            &__mobile{
                bottom: 15%;
                right: 22%;
                width: 15%;
            }
            &__laptop{
                bottom: 10%;
                left: 5%;
                width: 30%;
            }
    
        }
    }

    &-2 {
        &__laptop{
            width: 30%;
            height: auto;
            fill: $color-primary;

            &-svg {
                position: absolute;
                right: 10%;
                top: 40%;
                width: 40%;
                height: auto;
                z-index: -1;
            }

            @media only screen and (max-width: $screen-large) {
                width: 25%;
                &-svg {
                    top: 50%;
                    width: 40%;
                }
            }
        }
    }

    &-3 {
        &__mobile{
            width: 20%;
            height: auto;
            fill: $color-primary;
            &-svg {
                position: absolute;
                width: 25%;
                top: 40%;
                left: 20%;
                height: auto;
                z-index: -1;
            }

            @media only screen and (max-width: $screen-large) {
                width: 25%;
                &-svg {
                    width: 30%;
                    top: 50%;
                    left: 10%;
                }
            }
        }
    }

    &-4 {
        &__paint{
            width: 20%;
            height: auto;
            fill: $color-primary;

            &-svg {
                position: absolute;
                width: 40%;
                top: 50%;
                right: 30%;
                height: auto;
                z-index: -1;
            }

            @media only screen and (max-width: $screen-large) {
                width: 25%;

                &-svg {
                    width: 50%;
                    right: 20%;
                }
            }


        }


    }
}

.expertise {
    width: 80%;
    margin: auto;


    @media only screen and (max-width: $screen-large) {
        width: 90%;
    }
    @media only screen and (max-width: $screen-medium) {
        width: 100%;
    }

    h1 {
        font-size: 7rem;
        color: $color-primary;
        margin: 0rem;
    }

    p {
        font-size: 1.6rem;
        color: $color-primary;
        margin: 0rem;
    }
    

    &__section{

        &__content {
            display: flex;
            flex-direction: column;
        }


        &-1 {
            display: flex;
            flex: 1;
            justify-content: center;
            align-items: center;
            position: relative;

            h1 {
                font-size: 10rem;
                color: $color-primary;
            }

            &__svg {
                position: absolute;
                height: auto;
                fill: $color-primary;
            }
        }

        &-2, &-3, &-4 { 
            display: flex;
            flex: 1;
            justify-content: center;
            position: relative;
            flex-direction: column;
            align-items: center;
        }



    }
}