$color-primary: #45B3FF;
$color-secondary: #EFF8FC;
$color-white: #ffffff;
$color-bg: #E8F1F5;
$screen-large: 800px;
$screen-medium: 600px;
@import 'animations';

html {
    font-size: 10px;
    background-color: $color-bg;
    @media only screen and (max-width: $screen-large) {
        font-size: 8px;
    }
    @media only screen and (max-width: $screen-medium) {
        font-size: 8px;
    }
}

body {
    box-sizing: border-box;
    background-color: $color-bg;
    margin: 0px;
    overflow: hidden;
}

h1, strong, button, span {
    font-family: 'Caveat Brush', cursive;
}

h2, p, label {
    font-family: 'Quicksand', sans-serif;
}

section.body {
    padding: 10px;
    box-sizing: border-box;
}


section {
    min-height: 90vh;
    width: 80%;
    margin: auto;
    @media only screen and (max-width: $screen-large) {
        width: 90%;
    }

    @media only screen and (max-width: $screen-medium) {
        width: 100%;
    }
}

#logo-bg {
    position: absolute;
    top: 0%;
    left: -10%;
    z-index: -1;
}

#nav-bg {
    position: absolute;
    top: -1%;
    right: 0%;
    z-index: -1;
}

.nav-bg {
    &-transparent {
        background-color: 'transparent';
        top: 5%;
        animation-name: bgFadeOut;
        animation-duration: 1s;
        animation-fill-mode: forwards;
    }

    &-primary {
        background-color: $color-primary;
        top: 0%;
        animation-name: bgFadeIn;
        animation-duration: 1s;
        animation-fill-mode: forwards;
       animation-timing-function: ease-in;
    }
}

.navbar {
    flex: 1;
    position: fixed;
    width: 100%;
    left: 0%;
    box-sizing: border-box;
    padding: 2rem;
    z-index: 1000;
    top: 5%;

    &__content {
        display: flex;
        width: 80%;
        margin: auto;

        @media only screen and (max-width: $screen-large) {
            width: 90%;
        }

        @media only screen and (max-width: $screen-medium) {
            width: 100%;
        }
        
    & > .col {
        display: flex;
        
        &-left {
            flex: 1;
            align-items: center;

            @media only screen and (max-width: $screen-medium) {
                flex: .5;
            }
        }

        &-right {
            flex: 1;
            justify-content: space-between;
            align-items: center;
        }
    }

    strong, span, button {
        color: $color-white;
        font-size: 2rem;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
            cursor: pointer;
        }
    }

    button {
        border: none;
        background: none;
        &:hover {
            cursor: pointer;
            color: $color-primary;
            &::before {
                content: '';
                position: absolute;
                background-color: $color-secondary;
                width: 100%;
                height: 100%;
                padding: 1rem;
                border-radius: 10px;
                z-index: -1;
                animation-name: expand;
                animation-duration: .5s;
                animation-fill-mode: forwards;
            }
        }
    }

    .hover {
            color: $color-primary;
                &::before {
                    content: '';
                    position: absolute;
                    background-color: $color-secondary;
                    width: 100%;
                    height: 100%;
                    padding: 1rem;
                    border-radius: 10px;
                    z-index: -1;
                    animation-name: expand;
                    animation-duration: .5s;
                    animation-fill-mode: forwards;
                }
        }

    p {
        color: $color-white;
        font-size: 1.4rem;
        font-weight: bold;
    }

}
}

.logo {
    position: relative;
    margin-right: 1rem;
}

#scroll-shape {
    height: auto;
    width: 2.5rem;
}

.scroll {
    position: absolute;
    bottom: 0%;
    right: 0%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 2rem;
    animation-name: upAndDown;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;

    strong {
        font-size: 2rem;
        margin: 0rem;
        margin-bottom: 1rem;
        color: $color-white;
        font-weight: bold;
        text-align: center;
    }
}

.header {
    display: flex;
    flex: 1;
    align-items: stretch;
    justify-content: stretch;
    width: 100%;
    background: linear-gradient(89.79deg, #0162A4 -16.09%, #82CCFF 103.85%);
    flex-direction: column;
    min-height: 100vh;

    &__content {
        width: 80%;
        margin: auto;
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: center;
        position: relative;

        @media only screen and (max-width: $screen-medium) {
            bottom: 10vh;
        }
    }

    &__text {
        position: absolute;
        bottom: 5%;
        left: 0%;
        box-sizing: border-box;
        padding: 2rem;
    }

    &__avatar {
        width: 50%;
        height: auto;

        @media only screen and (max-width: $screen-large) {
            width: 70%;
        }

        @media only screen and (max-width: $screen-medium) {
            width: 80%;
        }
    }

    h2 {
        color: $color-white;
        font-size: 2em;
    }

    h1 {
        color: $color-white;
        font-size: 6rem;
        margin: 0rem 0rem 0rem 0rem;
    }
}

.backToTop {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    bottom: 0px;
    width: min-content;
    margin: 0rem auto 2rem auto;
    transition: bottom .5s;
    border: none;
    background: none;

    &:hover {
        cursor: pointer;
        transition: bottom .5s;
        bottom: 10px;
    }
    
    p {
        position: relative;
        top: 5px;
        text-align: center;
        color: $color-primary;
        font-size: 1.6rem;
        margin: 0rem;
    }
}


@import 'expertise';
@import 'project';
@import 'contact';
@import 'chevron';