.contact {
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-height: 80vh;

    @media only screen and (max-width: $screen-large) {
        width: 90%;
    }

    @media only screen and (max-width: $screen-medium) {
        width: 100%;
    }


    h1 {
        font-size: 7rem;
        color: $color-primary;
        margin: 0rem;
        text-align: center;
    }

    p {
        font-size: 1.6rem;
        color: $color-primary;
        margin: 0rem;
    }

    label {
        font-size: 1.6rem;
        color: $color-primary;
        margin: 0rem 0rem 1rem 0rem;
    }

    .form {
        width: 80%;
        box-sizing: border-box;
        padding: 5rem;
        background-color: $color-secondary;
        border-radius: 10px;

        @media only screen and (max-width: $screen-large) {
            width: 90%;
        }

        @media only screen and (max-width: $screen-medium) {
            width: 100%;
        }

        p {
            margin-bottom: 2rem;
        }

        input, textarea {
            width: 100%;
            border: none;
            border-radius: 10px;
            background-color: $color-bg;
            padding: 1rem;
            color: $color-primary;
            margin-bottom: 2rem;
            margin-top: 5px;
            font-size: 1.6rem;

            &::placeholder {
                color: #e8e8e8;
            }
        }

        .submit {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-top: 2rem;
        }
        button {
            border: none;
            border-radius: 10px;
            padding: 1rem;
            background-color: $color-primary;
            color: $color-white;
            width: 20%;
            font-size: 2.2rem;
        }
    }
}