@keyframes bgFadeIn {
    from {
        background-color: 'transparent';
        top: 5%;
    }

    to {
        background-color: $color-primary;
        top: 0%;
    }
}


@keyframes bgFadeOut {
    from {
        background-color: $color-primary;
        top: 0%;
    }
    to {
        background-color: 'transparent';
        top: 5%;
    }
}

@keyframes expand {
    from { 
        width: 0%;
        height: 0%;
    }
    to {
        width: 100%;
        height: 100%
    }
}

@keyframes contract {
    from { 
        width: 100%;
        height: 100%;
    }
    to {
        width: 0%;
        height: 100%;
    }
}

@keyframes slideBottomToUp {
    0% {
        bottom: -50vh;
        opacity: 0;
    }
    
    100% {
        bottom: 0vh;
        opacity: 1;
    }
}

@keyframes upAndDown {
    0% {
        bottom: 0%;
    }

    50% {
        bottom: 3%;
    }

    90% {
        bottom: 0%;
    }
    100% {
        bottom: 0%;
    }
}

.slideBottomToUp {
    position: relative;
animation-name: slideBottomToUp;
animation-timing-function: ease-in-out;
animation-fill-mode: forwards;
opacity: 0;
animation-duration: 1s;   
animation-delay: .5s;
}