#projects-svg {
    width: 70%;
    height: auto;
}

.projects {
    width: 80%;
    margin: auto;
    margin-top: 15vh;
    box-sizing: border-box;

    @media only screen and (max-width: $screen-large) {
        width: 90%;
    }
    @media only screen and (max-width: $screen-medium) {
        width: 100%;
    }

    h1 {
        font-size: 7rem;
        color: $color-primary;
        margin: 0rem;
    }

    p {
        font-size: 1.6rem;
        color: $color-primary;
        margin: 0rem;
    }

    & > section {
        min-height: 95vh;
    }

    &__section {
        box-sizing: border-box;

        &-1 {
            display: flex;
            flex-direction: column;
            justify-content: center;

            h1 {
                text-align: left;
                font-size: 10rem;
            }

            & > .col {
                display: flex;
    justify-content: center;

                &:first-child {
                    margin-bottom: 5rem;
                }
            }
        }

        &-2, &-3, &-4 {
            display: flex;
            flex-direction: column;
            justify-content: center;

            &__imgs {
                display: flex;
                margin-top: 3rem;

                & > .col {
                    flex: 1;
                    height: min-content;
                }

                img {
                    width: 100%;
                    height: auto;
                    border-radius: 5px;
                }
            }
        }

    }
}